@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&display=swap);
.lateral {
  width: 20%;
  height: 100%;
  background-color: #040b14;
  display: flex;
  align-items: center;
  flex-direction: column;
}

.lateral > .img {
  background-color: #e4dcdc;
  width: 140px;
  height: 140px;
  border-radius: 70px;
  margin: 0 auto;
  margin-top: 10%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.lateral > .img img {
  width: 130px;
  height: 130px;
  border-radius: 65px;
}

.lateral p {
  font-size: 1.3em;
  font-family: 'Open Sans', sans-serif;
  font-weight: 700;
  color: white;
  margin: 5% 0;
}

.lateral .container {
  width: 100%;
  height: 20%;
  margin: 2%;
  display: flex;
  justify-content: space-evenly;
  cursor: pointer;
}

.lateral .container .bola {
  width: 40px;
  height: 40px;
  border-radius: 20px;
  background-color: #e4dcdc;
  color:  #040b14;
  display: flex;
  justify-content: center;
  align-items: center;
}


.portifolio {
  width: 80%;
  height: 100%;
  padding: 1% 2%;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
}

h1, h2, h3, p, strong, a {
  font-family: 'Open Sans', sans-serif;
}

.content {
  margin: 1%;
  display: flex;
  flex-direction: column;
}

a {
  margin: 0.5em;
  cursor: pointer;
}

a:visited {
  font-family: 'Open Sans', sans-serif;
  color: black;
}

p {
  margin: 0.2em 0;
}

h1 {
  font-size: 1.8em;
  font-weight: 700;
  margin: 0 0 1% 0;
}

div.after-portifolio {
  box-sizing: border-box;
  width: 70%;
  height: 0.2em;
  background: #149ddd;
  margin: 0;
}

.backend, .frontend, .algoritms, .artigos {
  padding: 1% 3%;
}

div.after {
  box-sizing: border-box;
  width: 7%;
  height: 0.2em;
  background: #149ddd;
  margin: 0;
} 

h2 {
  font-size: 1.4em;
  font-weight: 700;
  margin: 1% 0 1% 0;
}

video {
  margin-top: 2%
}

img {
  width: 400px;
  margin: 2% 0;
}
.App, body {
  height: 100vh;
  margin: 0;
  padding: 0;
  width: 100vw;
  background-color: #d4cfec;
  display: flex;
  justify-content: start;
}

